import React from 'react';

import './App.css';
import { useState } from 'react'
import { PlayingArea } from './PlayingArea';
import {Layout} from 'antd';
import pic from './bayes.jpg'
const {Header,Sider,Content} =Layout;





function App() {

  const [state,changeState]=useState(Date.now())
  const [winningGames,changeWinningGames]=useState(0)
  const [switchingWins,ChangeSwitchingWins]=useState(0)
  const [totalGame,changeTotalGame]=useState(0)
  const [collapsed,changeCollapse]=useState(0)
  const resetGame=()=>
  {
        changeState(Date.now())
  }
  const gameEnd=(win,switchwin)=>
  {
        let y=switchingWins+switchwin
        
        let x=winningGames+win;
        changeTotalGame(totalGame+1)
        ChangeSwitchingWins(y)
        changeWinningGames(x)
  }


  return (
      <>
      <Layout>
      <Header   style={{
          position: 'fixed',
          top: 0,
          zIndex: 1,
          width: '100%',
        }}>
          <div className='logo'>GROUP 3</div>
          <li><p>Yuxin Hu: hu.yuxin3@northeastern.edu</p></li>
          <li><p>Yufei Huang: huang,yufe@northeasyern.edu</p></li>
          <li><p>Jiameng Ji:  ji.jiam@northeastern.edu</p></li>
          <li><p>Yiqing Jiang:  jiang.yiqi@northeastern.edu</p></li>
          <li><p>Chengxu Lan: lan.che@northeastern.edu</p></li>
          </Header>
      </Layout>
      <Layout>
            <Sider  collapsible  breakpoint="xs"
        collapsedWidth="0"
         width={300} style={{
          position: 'fixed',
          marginTop:'50px',
          zIndex: 2,
          width:'700px',
          height:'100%'
        }} >
                <div className='container-1'>
    <div className='container-2'>
  <table class="table table-hover">
  <tbody>
    <tr>
     <th width="5%">TOTAL GAME</th>
     <td width="5%">{totalGame}</td>
    </tr>
    <tr>
     <th width="5%">WIN</th>
     <td width="5%">{winningGames}</td>
     </tr>
     <tr>

     <th width="5%">LOSE</th>
     <td width="5%">{totalGame-winningGames}</td>
     </tr>
     <tr>
     <th width="5%">WIN %</th>
     <td width="5%">{(totalGame>0?parseFloat((winningGames/totalGame)*100).toFixed(2):0)}%</td>
     </tr>
     <tr>
     <th width="5%">Switch Wins</th>
     <td width="5%">{switchingWins}</td>
     </tr>
     <tr>
     <th width="5%">Switch Wins%</th>
     <td width="5%">{(totalGame>0?parseFloat((switchingWins/totalGame)*100).toFixed(2):0)}%</td>
     </tr>
     <tr>
     <th width="5%">Stick Wins</th>
     <td width="5%">{winningGames-switchingWins}</td>
     </tr>
     <tr>
     <th width="5%">Stick Wins%</th>
     <td width="5%">{(totalGame>0?parseFloat(((winningGames-switchingWins)/totalGame)*100).toFixed(2):0)}%</td>
    </tr>
    <tr>
    </tr>
    <tr>
    
    </tr>
  </tbody>
</table>
</div>
<div className='btn-container'><button  onClick={()=>{resetGame()}} className='btn btn-dark custom-btn'>RESET</button></div>
<div >
           
  </div>
</div>
        </Sider>
             
            <Content className='content'>
            <div className='body'>
      <h1>Monty Hall Game</h1>
      <p>Trying to find the best strategy to win in this game is a famous brain teaser, not least because so many people get it wrong. Let's Make a Deal and named after its original host, Monty Hall.</p>
      <h4>The game is simple:</h4>

      <p>you are shown three closed doors. Behind one of them is a car, behind the two others, a cow. You first pick one door, but it does not open right away. The game host, Monty Hall, who knows behind which door the car is waiting, then teases you by opening one of the two doors that you had not picked to show you the cow sitting there. Then he may offer you a choice: staying with your original pick, or switching to the third remaining door.</p>
      <h4>The question is: </h4>
      <p>Should you switch or shouldn't you?</p>

       <PlayingArea key={state} resetGame={resetGame} gameEnd={gameEnd} totalGame={totalGame} winningGames={winningGames} switchingWins={switchingWins}/>
      <h1>Using Bayes’ Theorem to Solve the Monty Hall Problem</h1>

      <p>Let’s assume you pick door 1 and then Monty shows you the goat behind door 2. In order to use Bayes’ Theorem we need to first assign an event to A and B.</p>
      <ol>
      <li><p>Let event A be that the car is behind door number 1.</p></li>
      <li><p>Let event B be that Monty opens up door 2 to show the goat.</p></li>
      </ol>
      <h4>Here’s the Bayes’ solution</h4>
      <img src={pic} className='img'></img>
     <p>Pr(A) is pretty simple to figure out. There is a 1/3 chance that the car is behind door 1. There are two doors left, and each has a 1/2 chance of being chosen — which gives us Pr(B|A), or the probability of event B, given A.Pr(B)</p>
     <p>Our Monty Hall game simulation allows you to test this out for yourself and see the results for yourself. Simply choose a door and see if you win the prize. You can play as many times as you like and see how often you come out on top</p>

    </div>
           </Content>
      </Layout>
      </>
  );
}

export default App;
