import React, { useEffect } from 'react'
import { useState } from 'react'
// Exports a functional component named 'Door'
/**
 *id: An identifier for the door, likely used to distinguish between multiple doors.
 *onclick: A function that will be called when the door is clicked.
 *isOpen: A boolean that determines whether the door is open or closed.
 */
export const Door = ({id,onclick,isOpen}) => {

  // cssClasses: An array of class names for styling the door.
  // 'door' is a default class.
  // 'doorOpen' is conditionally added based on the 'isOpen' prop.

  const cssClasses=["door",isOpen?"doorOpen":""]
  return (
    // A div representing the door.
    // - onClick handler: Triggers 'onclick' function passed in props with the 'id' of the door.
    // - className: Combines the classes from cssClasses array for styling.
    
    <div onClick={()=>{onclick(id)}} className={cssClasses.join(' ')}>{!isOpen&&<div><b>Open</b></div>}</div>
  )
}
